import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import bgImage from "../assets/images/shapes/testimonials-map-1-1.png";
import Loader from "../components/cardLoader";
import Layout from '../components/layout'
import { Link } from 'gatsby'
import { useMediaQuery } from 'react-responsive'


const chunk = (arr, len) => {

  var chunks = [],
    i = 0,
    n = arr.length;

  while (i < n) {
    chunks.push(arr.slice(i, i += len));
  }

  return chunks;
}

const TestimonialsOne = ({ pageContext }) => {


  const [chunkSize,  setChunkSize] = useState(1)
  const handleMediaQueryChange = (matches) => {
    if(matches) {
      setChunkSize(3)
    } else {
      setChunkSize(1)
    }
  }
  
  const isDesktopOrLaptop = useMediaQuery({
    minWidth: 1224
  }, undefined, handleMediaQueryChange)

  const { items: _items } = pageContext;
  const c2 = chunk(_items, 1).map(
    (arr, ii) => {
      return <Container className="m-10"><Row>
        <Col lg={2}></Col>
        {
          arr.map((props, index) => {
            const { image, progressCount, raised, goal, title, text, link, number } = props;
            return <Col lg={8} key={`testimonials-post-key-${ii} bojio-card`}>
              <div className="cause-card" key={`cause-card-key-${index}`}>
              <div className="cause-card__inner" style={{ height: '260px'}}>
                <div className="cause-card__image">
                  <img src={image} alt="" />
                </div>
                <div className="p-4">
                  <h2>Singapore Mobile Number</h2>
                  <h3>
                    {number}
                  </h3>
                  <div className="cause-card__top">


                  </div>
                  <h3>
                    <Link to={link}>{title}</Link>
                  </h3>
                  <p>{text}</p>
                  <div className="cause-card__bottom">
                    <Link className="thm-btn ml-auto"  to={`https://wa.me/6587413159?text=I'm%20interested%20in%20${number}`}>
                      I want this!
                    </Link>
                    {
                      "" && <Link className="cause-card__share" to="#">
                        <i className="azino-icon-share"></i>
                      </Link>
                    }

                  </div>
                </div>
              </div>
            </div>
            </Col>
          })
        }

      </Row>
      </Container>
    }
  )
  const [items, setItems] = useState(c2)
  const [isNextPageLoading, setIsNextPageLoading] = useState(false)
  return (<Layout>
    <section
      className="testimonials-one pt-90 pb-90"
      style={{
        backgroundImage: `url(${bgImage})`

      }}
    >
      <Loader
        hasNextPage={false}
        isNextPageLoading={isNextPageLoading}
        items={items}
        sortOrder={'asc'}
        loadNextPage={() => {
          setIsNextPageLoading(true)
          setItems([
            ...items,
          ])
          setIsNextPageLoading(false)
        }}
      />
    </section>
  </Layout>
  );
};

export default TestimonialsOne;
